<template>
  <div class="header_bg">
    <header class="header_container">
      <div class="logo">
        <img src="https://arrgrr.github.io/pinjet/img/logo.jpg" alt="三俱精密" />
      </div>
      <div class="btn_group">
        <router-link class="navBtn" @click="isOpen = true" to="/en/index">HOME</router-link>
        <router-link class="navBtn" @click="isOpen = true" to="/en/about">ABOUT</router-link>
        <router-link class="navBtn" @click="isOpen = true" to="/en/product">PRODUCT</router-link>
        <router-link class="navBtn" @click="isOpen = true" to="/en/contact">CONTACT</router-link>
        <router-link class="navBtn_search" v-if="isOpen == false" to="/en/search">
          <div @click="isOpen = true">
            <font-awesome-icon class="icon" :icon="['fal', 'search']" />
          </div>
        </router-link>
        <a class="navBtn_search" v-if="isOpen == true" @click="$router.go(-1),isOpen = false">
          <font-awesome-icon class="icon" :icon="['fal', 'times']" />
        </a>
        <router-link to="/index" class="navBtn_search">中</router-link>
      </div>
    </header>
    <header class="mobile_header">
      <div class="logo">
        <img src="https://arrgrr.github.io/pinjet/img/logo.jpg" alt="三俱精密" />
      </div>
      <div @click="openMenu" v-if="menuOpen == false" class="drop_btn">
        <font-awesome-icon class="icon" :icon="['fal', 'bars']" />
      </div>
      <div @click="openMenu" v-if="menuOpen == true" class="drop_btn">
        <font-awesome-icon class="icon" :icon="['fal', 'times']" />
      </div>
      <div id="menu" class="btn_group">
        <router-link class="navBtn" @click.native="openMenu" to="/en/index">HOME</router-link>
        <router-link class="navBtn" @click.native="openMenu" to="/en/about">ABOUT</router-link>
        <router-link class="navBtn" @click.native="openMenu" to="/en/product">PRODUCT</router-link>
        <router-link class="navBtn" @click.native="openMenu" to="/en/contact">CONTACT</router-link>
        <div class="m_searcher">
          <router-link @click.native="openMenu" class="navBtn_search" to="/search">
            <font-awesome-icon class="icon" :icon="['fal', 'search']" />
          </router-link>
          <router-link to="/index" class="navBtn_search">中</router-link>
        </div>
      </div>
    </header>
  </div>
</template>


<script>
export default {
  data() {
    return {
      isOpen: false,
      menuOpen: false
    };
  },
  methods: {
    openSearch() {
      this.isOpen = false;
    },
    openMenu() {
      var nowH = document.getElementById("menu");
      if (nowH.offsetHeight == 0) {
        nowH.style.height = "284px";
        this.menuOpen = true;
      } else {
        nowH.style.height = "0";
        this.menuOpen = false;
      }
    }
  }
};
</script>
