    <div class="search_container">
      <div class="input_container">
        <font-awesome-icon class="arrow" :icon="['fal', 'search']" />
        <input class="search_input" type="text" v-model="filterInput" placeholder="Search product" />
      </div>
      <div class="list_container">
        <div class="list_column">
          <template v-for="item in filterArray">
            <router-link class="link_item" :to="item.linkpath" :key="item.index">
              {{item.name}}
              <font-awesome-icon class="icon" :icon="['fal', 'arrow-right']" />
            </router-link>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
