        { name: "ESD coating", linkpath: "/en/product/esd_coating" }
      ]
    };
  },
  computed: {
    filterArray: function() {
      var vm = this;
      return vm.list.filter(function(item) {
        return item.name.match(vm.filterInput);
      });
    }
  }
};
</script>
